<template>
    <div class="search-container-layout">
        <div class="search-container">
            <div class="result-keyword-box" v-if="keywordResult">
                <div class="keyword-text">
                    <span>"{{keywordResult}}"</span>에 대한 통합 검색결과가 <br class="tablet-mobile-element">총 <span>{{totalCount}}건</span>
                    조회되었습니다
                </div>
                <v-btn class="blackButton" @click="totalSearchOpen()"><span>상세검색</span></v-btn>
            </div>
            <div class="article-total-box">
                <ul class="tab">
                    <li :class="totalActive" @click="setTab('total')">통합검색 ({{totalCount}})</li>
                    <li v-for="tab in tabList" :class="tab.active" @click="setTab(tab.code)">
                        {{tab.name}}
                        ({{util.numberFormat(tab.data.total)}})
                    </li>
                </ul>

                <ul class="board-container">
                    <li class="board-item" v-for="(tab,index) in tabList" :id="'tab-article-list-'+tab.code">
                        <div class="board-name">
                            {{tab.name}}
                            ({{util.numberFormat(tab.data.total)}}건)
                        </div>
                        <div class="list-bottom-line" v-if="tab.data.list.length>0">
                            <ul :class="'article-container '+tab.code">
                                <li v-for="(item,index) in tab.data.list" @click="articleView(item)">
                                    <div class="article-data" v-if="tab.code!='img' ">
                                        <div class="subject" v-if="tab.code=='article'">
                                        <span v-if="item.open=='no'">
                                            <font-awesome-icon style="font-size:12px;color:#666"
                                                               icon="fa-solid fa-lock"/>
                                        </span>
                                            <div class="subject-value" v-html="item.subject"></div>
                                        </div>
                                        <div class="desc" v-if="tab.code!='file'" v-html="item.content"></div>
                                        <div class="keyword" v-if="tab.code=='article' && item.keywords"
                                             v-html="item.keywords"></div>
                                        <div class="file" v-if="tab.code=='file'">
                                            {{item.fileName}}
                                        </div>
                                        <div class="author">
                                            작성자<span class="text">{{item.userName}}</span>
                                            작성일자<span
                                                class="text">{{item.createAt.replace('T', ' ').substring(1, 16)}}</span>
                                            <span v-if="tab.code!='comment'" class="pc-tablet-element">조회수<span
                                                    class="text">{{item.hit}}</span></span>
                                        </div>
                                    </div>
                                    <div class="article-data" v-else-if="tab.code=='img'">
                                        <div class="img" v-if="item.imgUrl">
                                            <img :src="item.imgUrl">
                                        </div>
                                        <div class="img-name">{{item.imgName}}</div>
                                        <div class="author">
                                            작성자<span class="text">{{item.userName}}</span>
                                        </div>
                                    </div>

                                </li>
                            </ul>
                            <paging :paging="tab.data.paging" scrollNone="yes" :addPrams="{code:tab.code}"
                                    :reload="tab.data.pagingReload"
                                    @pageMove="pageBoardMode"></paging>
                        </div>
                        <div class="no-data" v-else>
                            검색 결과가 없습니다
                        </div>
                    </li>
                </ul>
            </div>

            <articleUpassComponent :bid="bid" :articleId="articleId" @upassSuccess="viewSuccess"
                                   :modelOpen="passDialogShow"
                                   @parentOpenFalse="passDialogShow=false"></articleUpassComponent>


            <v-dialog
                    v-model="searchDialog"
                    width="1300"
                    persistent
                    rounded
                    style="z-index:100;"
            >
                <div class="modal-layout" style="height:800px;background: #fff;">
                    <div class="modal-header">
                        <div class="modal-title"></div>
                        <div class="modal-close" @click="searchDialog=false">
                            <font-awesome-icon icon="fa-solid fa-circle-xmark"/>
                        </div>
                    </div>
                    <div class="modal-body" id="articleView-container">
                        <searchComponent :keyword="keyword" v-if="searchDialog"></searchComponent>
                    </div>
                </div>
            </v-dialog>
        </div>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_ALL_SEARCH,
        API_URL_GET_PART_SEARCH
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import UTIL from '@/common/util'
    import articleUpassComponent from '@/components/articleUpass'
    import {mapGetters} from "vuex";
    import searchComponent from "@/views/WEB/search/search";
    import paging from '@/components/paging'
    import JQ from "jquery"

    export default {
        name: 'mainSearch',
        components: {
            articleUpassComponent,
            searchComponent,
            paging
        },
        computed: {
            ...mapGetters('config', ["getMenu", "getSettingActive"]),
        },
        watch: {
            getSettingActive(val) {
                if (val) {
                    this.getMcode();
                }
            },
            searchDialog(val) {
                if(val) {
                    JQ('body').css('position','fixed')
                } else {
                    JQ('body').css('position','static')
                }

            }
        },
        data() {
            return {
                util: UTIL,
                keyword: '',
                memberInfo: {},
                passDialogShow: false,
                keywordResult: '',
                tabCode: 'total',
                bid: '',
                searchDialog: false,
                articleId: '',
                totalActive: 'active',
                totalCount: 0,
                tabList: [
                    {
                        name: '문서',
                        code: 'article',
                        active: '',
                        data: {
                            total: 0,
                            list: [],
                            page: 1,
                            paging: {},
                            pagingReload: false,
                        }
                    },
                    {
                        name: '댓글',
                        code: 'comment',
                        active: '',
                        data: {
                            total: 0,
                            list: [],
                            page: 1,
                            paging: {},
                            pagingReload: false,
                        }
                    },
                    {
                        name: '이미지',
                        code: 'img',
                        active: '',
                        data: {
                            total: 0,
                            list: [],
                            page: 1,
                            paging: {},
                            pagingReload: false,
                        }
                    },
                    {
                        name: '파일',
                        code: 'file',
                        active: '',
                        data: {
                            total: 0,
                            list: [],
                            page: 1,
                            paging: {},
                            pagingReload: false,
                        }
                    },
                ],
                limit: 10,
                page: 1,
                total: 0,
                paging: {},
                pagingReload: false,
                boardIds: {},
            };
        },
        created() {
            this.$eventBus.$on('searchKeyword', this.eventSearch)
            this.keyword = this.$route.params.keyword;

            const member = AUTH.getSession();
            if (member && member.memberInfo) {
                this.memberInfo = member.memberInfo;
            }
            if (this.getSettingActive) {
                this.getMcode();
            }
            this.search();
        },
        methods: {
            setTab(code) {
                if (code == 'total') {
                    this.tabCode = 'total';
                    this.totalActive = 'active';
                    JQ('.board-container .board-item').show();

                } else {
                    this.totalActive = '';
                    JQ('.board-container .board-item').hide();
                }
                this.tabList.forEach((item) => {
                    if (item.code == code) {
                        item.active = 'active'
                        this.tabCode = code;
                        JQ('#tab-article-list-'+code).show();
                    } else {
                        item.active = ''
                    }
                })

            },
            getMcode() {
                for (let menu of this.getMenu) {
                    if (menu.muse != 'yes') continue;
                    if (menu.subList && menu.subList.length > 0) {
                        for (let subMenu of menu.subList) {
                            if (subMenu.muse != 'yes') continue;
                            if (subMenu.linkType == 'board') {
                                this.boardIds[subMenu.boardId] = {mainCode: menu.code, subCode: subMenu.code};
                            }
                        }

                    } else {
                        if (menu.linkType == 'board') {
                            this.boardIds[menu.boardId] = {mainCode: menu.code};
                        }
                    }
                }

            },
            totalSearchOpen() {
                this.searchDialog = true;
            },
            eventSearch(params) {
                this.keyword = params.keyword;
                this.$router.push('/search/' + this.keyword).catch(() => {
                });
                this.search();

            },
            search() {

                if (!this.keyword) return;
                let searchBids = [];
                for (const key in this.boardIds) {
                    searchBids.push(key)
                }
                let frm = {
                    keyword: this.keyword,
                    searchBids: searchBids,
                    limit: this.limit.toString()
                }
                this.$eventBus.$emit('overlay', 'open');
                this.tabList[0].data.page = 1
                this.tabList[1].data.page = 1
                this.tabList[2].data.page = 1
                this.tabList[3].data.page = 1
                this.page = 1;

                this.serverFlag = true;
                this.setTab('total')
                apiService.post(API_URL_GET_ALL_SEARCH, frm, (res) => {
                    this.keywordResult = this.keyword;
                    this.$eventBus.$emit('overlay', 'hide');
                    this.serverFlag = false;
                    if (res.status == 'success') {
                        this.setAllDataList(res, 'all')
                    }
                });
            },
            setAllDataList(res, code, page) {
                let total = 0;
                let k = 0;
                for (let item of this.tabList) {
                    if (code == 'all') {
                        switch (item.code) {
                            case 'article':
                                item.data.total = res.articleData.total
                                item.data.list = [];
                                for (let article of res.articleData.list) {
                                    article.content = UTIL.stripTagsNotEm(article.content);
                                    if (article.keywords) {
                                        let keywords = article.keywords.split(',');
                                        let keywordResults = [];
                                        for (let keytext of keywords) {
                                            if (keytext.indexOf(this.keyword) !== -1) {
                                                keywordResults.push(keytext.replace(keytext, '<em>' + this.keyword + '</em>'))
                                            }
                                        }
                                        if (keywordResults.length > 0) {
                                            article.keywords = '#' + keywordResults.join(', #')
                                        }

                                    }

                                    item.data.list.push(article)
                                }
                                item.data.paging = {
                                    total: res.articleData.total,
                                    currentPage: item.data.page,
                                    limit: this.limit,
                                    pageGroup: 10,
                                }
                                break;
                            case 'comment':
                                item.data.total = res.commentData.total
                                item.data.list = res.commentData.list
                                item.data.paging = {
                                    total: res.commentData.total,
                                    currentPage: item.data.page,
                                    limit: this.limit,
                                    pageGroup: 10,
                                }
                                break;
                            case 'img':
                                item.data.total = res.articleImageData.total
                                item.data.list = [];
                                for (let imgData of res.articleImageData.list) {
                                    if (imgData.imgs) {
                                        let images = imgData.imgs.split(',');
                                        let imageNames = imgData.orgImgNames.split(',');
                                        let searchImageNames = imgData.searchImgNames.split(',');
                                        let imgUrl = '';
                                        let imgName = '';
                                        let imgIndex = 0;
                                        for (let searchName of searchImageNames) {
                                            if (searchName.indexOf(this.keywordResult) !== -1 && !imgUrl) {
                                                imgUrl = images[imgIndex];
                                                imgName = imageNames[imgIndex];
                                            }
                                            imgIndex++;
                                        }
                                        imgData.imgUrl = apiService.imgHost + 'api/controller/board/getArticleImage?imgName=' + encodeURIComponent(imgUrl)
                                        imgData.imgName = imgName;
                                    }
                                    item.data.list.push(imgData)
                                }

                                item.data.paging = {
                                    total: res.articleImageData.total,
                                    currentPage: item.data.page,
                                    limit: this.limit,
                                    pageGroup: 10,
                                }

                                break;
                            case 'file':
                                item.data.total = res.articleFileData.total
                                item.data.list = [];
                                for (let fileData of res.articleFileData.list) {
                                    if (fileData.dfileOrgNames) {
                                        let files = fileData.dfileOrgNames.split(',');
                                        let fileName = '';
                                        for (let searchName of files) {
                                            if (searchName.indexOf(this.keywordResult) !== -1 && !fileName) {
                                                fileName = searchName;
                                            }
                                        }
                                        fileData.fileName = fileName;
                                    }
                                    item.data.list.push(fileData)
                                }
                                item.data.paging = {
                                    total: res.articleFileData.total,
                                    currentPage: item.data.page,
                                    limit: this.limit,
                                    pageGroup: 10,
                                }
                                break;
                        }

                        item.data.pagingReload = true;
                        const selectIndex = k;
                        setTimeout(() => {
                            item.data.pagingReload = false;
                            this.$set(this.tabList, selectIndex, item)
                        }, 500)

                        total += parseInt(item.data.total)

                    } else if (item.code == code) {
                        item.data.total = res.resultData.total
                        item.data.list = res.resultData.list
                        item.data.paging = {
                            total: res.resultData.total,
                            currentPage: page,
                            limit: this.limit,
                            pageGroup: 10,
                        }
                        item.data.pagingReload = true;
                        const selectIndex = k;
                        setTimeout(() => {
                            item.data.pagingReload = false;
                            this.$set(this.tabList, selectIndex, item)
                        }, 500)
                        this.$set(this.tabList, k, item)
                    }
                    k++;
                }
                this.totalCount = UTIL.numberFormat(total)
            },
            articleView(item) {
                let viewFlag = false;
                if (!item.subject) {
                    this.commentDetail(item);
                    return;
                }
                if (item.open == 'no') {
                    if (this.memberInfo.role == 'ROLE_ADMIN') {
                        viewFlag = true;
                    } else {
                        if (item.userId) {
                            if (item.userId == this.memberInfo.uid) viewFlag = true;
                        } else {
                            this.articleId = item.id;
                            this.bid = item.bid;
                            this.passDialogShow = true;
                            return;
                        }
                    }
                } else {
                    viewFlag = true;
                }
                if (viewFlag) {
                    const tempParams = {
                        page: this.page,
                        category: this.category
                    }
                    //sessionStorage.setItem('tempParams', JSON.stringify(tempParams));
                    if (this.boardIds[item.bid]?.mainCode) {
                        const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                        this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.id)
                    }
                }
            },
            commentDetail(item) {
                if (this.boardIds[item.bid]?.mainCode) {
                    sessionStorage.setItem('viewComment', item.id);

                    const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                    this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.parentId)
                }

            },
            viewSuccess() {
                const tempParams = {
                    page: this.page,
                    category: this.category
                }
                //sessionStorage.setItem('tempParams', JSON.stringify(tempParams));
                if (this.boardIds[this.bid]?.mainCode) {
                    const subCode = (this.boardIds[this.bid].subCode) ? this.boardIds[this.bid].subCode : 'none';
                    this.$router.push('/board/articleView/' + this.boardIds[this.bid].mainCode + '/' + subCode + '/' + this.articleId)
                }
            },
            pageBoardMode(params) {
                this.searchPart(params)
            },
            searchPart(params) {
                let searchBids = [];
                for (const key in this.boardIds) {
                    searchBids.push(key)
                }
                let frm = {
                    keyword: this.keywordResult,
                    searchBids: searchBids,
                    code: params.addParams.code,
                    page: params.page.toString(),
                    limit: this.limit.toString()
                }
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_GET_PART_SEARCH, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.serverFlag = false;
                    if (res.status == 'success') {
                        let k = 0;
                        for (let item of this.tabList) {
                             if (item.code == params.addParams.code) {
                                item.data.total = res.resultData.total
                                item.data.list = res.resultData.list
                                item.data.paging = {
                                    total: res.resultData.total,
                                    currentPage: params.page,
                                    limit: this.limit,
                                    pageGroup: 10,
                                }
                                item.data.pagingReload = true;
                                const selectIndex = k;
                                setTimeout(() => {
                                    item.data.pagingReload = false;
                                    this.$set(this.tabList, selectIndex, item)
                                }, 500)
                                this.$set(this.tabList, k, item)
                            }
                            k++;
                        }
                    }
                });
            },
        },
        beforeDestroy() {
            this.$eventBus.$off('searchKeyword');

        },
        updated() {
            JQ('.subject em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-size': '18px',
                'font-style': 'normal'
            });
            JQ('.desc em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-style': 'normal'
            });
            JQ('.keyword em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-style': 'normal'
            });

        }

    }
</script>

<style lang="scss" scoped>
    .modal-layout {
        padding: 0;
        width: 100%;

        .modal-header {
            width: 100%;
            height: 34px;
            background: #f0f0f0;
            border-bottom: solid 1px #ccc;
            color: #000;
            display: flex;
            justify-content: space-between;
            line-height: 34px;

            .modal-title {
                padding-left: 25px;
                font-weight: bold;
                font-size: 17px;
            }

            .modal-close {
                padding-right: 7px;
                cursor: pointer;
                font-size: 20px;
                padding-top: 1px;
            }

        }

        .modal-body {
            max-height: 766px;
            overflow-y: auto;
            overflow-x: hidden;
            background: #fff;
        }

    }

    .search-container {
        width: 100%;
        min-height: calc(100vh - 300px);

        .result-keyword-box {
            display: flex;
            justify-content: space-between;

            .keyword-text {
                font-size: 24px;
                font-weight: bold;
                padding: 15px 0 40px 0;
                color: #999999;

                span {
                    font-size: 24px;
                    font-weight: bold;
                    color: #F4364C;
                }
            }

            .blackButton {
                min-width: 135px;
                width: 135px;
                height: 45px;
                max-height: 45px !important;
                margin-top: 10px;

                span {
                    font-size: 18px;
                }
            }
        }

        .article-total-box {
            margin: 0;

            .tab {
                padding: 0;
                display: flex;
                border-bottom: solid 1px #333;
                margin-bottom: 30px;

                li {
                    padding: 15px 20px;
                    line-height: 130%;
                    text-align: center;
                    border-right: solid 1px #333;
                    border-top: solid 1px #333;
                    cursor: pointer;
                    font-size: 14px;
                    background: #fff;
                    word-break: keep-all;

                    &:first-child {
                        border-left: solid 1px #333;
                    }

                    &.active {
                        background: #333;
                        color: #fff;
                    }
                }
            }

            .board-container {
                padding: 0;

                .board-item {
                    margin-bottom: 30px;
                    background: #fff;
                    padding: 30px;

                    .board-name {
                        font-weight: bold;
                        font-size: 18px;
                        color: #333333;
                        width: 100%;
                        padding: 0 0 15px 0;
                        border-bottom: solid 1px #333;
                        cursor: pointer;
                    }

                    .bottom-line {
                        border-bottom: solid 1px #dcdcdc;
                        padding-bottom: 30px;

                    }

                    .no-data {
                        font-weight: bold;
                        font-size: 18px;
                        color: #333333;
                        width: 100%;
                        text-align: center;
                        height: 150px;
                        line-height: 170px;
                    }

                    .article-container {
                        padding: 20px 0 0 0;

                        &.img {
                            display: flex;
                            flex-wrap: wrap;

                            li {
                                display: block;
                                padding: 10px 0 20px 0;
                                width: 18%;
                                margin-right: 28px;

                                &:nth-child(5n) {
                                    margin-right: 0;
                                }

                                .article-data {
                                    width: 100%;

                                    .img-name {
                                        padding: 5px 0 5px 0;
                                        font-size: 14px;
                                        color: #333;
                                        font-weight: bold;
                                    }

                                    .author {
                                        color: #888;
                                        font-size: 12px;

                                        span {
                                            font-size: 12px;
                                            color: #888;
                                        }
                                    }

                                    .img {
                                        width: 100%;
                                        height: 150px;
                                        overflow: hidden;

                                        img {
                                            width: 100%;
                                            min-height: 150px;
                                        }
                                    }
                                }


                            }
                        }

                        li {
                            display: flex;
                            cursor: pointer;
                            padding: 0 0 20px 0;

                            .article-data {
                                width: 100%;

                                .subject {
                                    font-size: 18px;
                                    color: #333;

                                    .subject-value {
                                        font-size: 18px;
                                        color: #333;

                                    }

                                }

                                .keyword {
                                    margin: 0 0 15px 0;
                                }

                                .desc {
                                    margin: 3px 0 15px 0;
                                    font-size: 14px;
                                    color: #333;
                                    display: -webkit-box;
                                    -webkit-line-clamp: 2;
                                    -webkit-box-orient: vertical;
                                    line-height: 1.4;
                                    height: 2.6em;
                                    overflow: hidden;
                                    text-overflow: ellipsis;

                                }

                                .file {
                                    padding: 5px 0 5px 0;
                                    font-size: 14px;
                                    color: #333;
                                    font-weight: bold;
                                }

                                .author {
                                    color: #888;
                                    font-size: 12px;

                                    span {
                                        font-size: 12px;
                                        color: #888;

                                        &.text {
                                            font-size: 12px;
                                            color: #333;
                                            padding: 0 15px 0 5px;
                                        }
                                    }
                                }
                            }

                            .img {
                                min-width: 84px;
                                width: 84px;
                                height: 84px;
                                overflow: hidden;

                                img {
                                    width: 100%;
                                    min-height: 84px;
                                }
                            }
                        }
                    }
                }
            }

        }
    }

    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .search-container-layout {
            border: solid 1px #F2F2F2;
            min-height: calc(100vh);
            padding-bottom: 50px;

            .search-container {
                min-height: calc(100vh - 50px);

                .result-keyword-box {
                    display: block;
                    text-align: center;
                    padding-bottom: 45px;

                    .keyword-text {
                        text-align: center;
                        padding: 15px 0 10px 0;
                    }

                }

                .article-container {
                    &.img {
                        li {
                            width: 23% !important;
                            margin-right: 1% !important;
                            margin-left: 1% !important;
                            &:nth-child(4n) {
                                margin-right: 0 !important;
                            }

                            &:nth-child(5n) {
                                margin-right: 1% !important;
                            }

                            .article-data {
                                width: 100%;

                                .img {
                                    height: 120px !important;
                                    overflow: hidden;

                                    img {
                                        width: 100%;
                                        min-height: 120px !important;
                                    }
                                }
                            }


                        }
                    }
                }
            }
        }
    }

